@font-face {
  font-family: LineIcons;
  src: url("LineIcons.83ba5249.eot");
  src: url("LineIcons.83ba5249.eot") format("embedded-opentype"), url("LineIcons.7f088059.woff2") format("woff2"), url("LineIcons.d9158aeb.woff") format("woff"), url("LineIcons.2d746faa.ttf") format("truetype"), url("LineIcons.7a9c8492.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

.lni {
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 1em / 1 LineIcons;
  display: inline-block;
}

.lni-sm {
  font-size: .8em;
}

.lni-lg {
  font-size: 1.2em;
}

.lni-16 {
  font-size: 16px;
}

.lni-32 {
  font-size: 32px;
}

.lni-bg-square, .lni-bg-circle {
  background-color: #eee;
  padding: .35em;
}

.lni-bg-circle {
  border-radius: 50%;
}

.lni-ul {
  padding-left: 0;
  list-style-type: none;
}

.lni-ul > li {
  align-items: flex-start;
  line-height: 1.4;
  display: flex;
}

.lni-ul > li > .lni {
  line-height: inherit;
  margin-right: .4em;
}

.lni-is-spinning {
  animation: 2s linear infinite lni-spin;
}

@-webkit-keyframes lni-spin {
  0% {
    -webkit-transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@-moz-keyframes lni-spin {
  0% {
    -moz-transform: rotate(0);
  }

  100% {
    -moz-transform: rotate(360deg);
  }
}

@keyframes lni-spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.lni-rotate-90 {
  filter: progid:DXImageTransform. Microsoft. BasicImage(rotation= 1);
  transform: rotate(90deg);
}

.lni-rotate-180 {
  filter: progid:DXImageTransform. Microsoft. BasicImage(rotation= 2);
  transform: rotate(180deg);
}

.lni-rotate-270 {
  filter: progid:DXImageTransform. Microsoft. BasicImage(rotation= 3);
  transform: rotate(270deg);
}

.lni-flip-y {
  filter: progid:DXImageTransform. Microsoft. BasicImage(rotation= 0);
  transform: scale(-1, 1);
}

.lni-flip-x {
  filter: progid:DXImageTransform. Microsoft. BasicImage(rotation= 2);
  transform: scale(1, -1);
}

